/* =======================
   Tailwind Base, Components, Utilities
======================= */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* =======================
   Google Fonts
======================= */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&family=Playfair+Display:wght@600;700&display=swap');

/* =======================
   Global Styles
======================= */
body {
  font-family: 'Poppins', sans-serif; /* Direct CSS, do NOT use @apply */
  background-color: #f9fafb; /* Tailwind bg-gray-50 fallback */
  color: #111827;            /* Tailwind text-gray-900 fallback */
}

h1, h2, h3 {
  font-family: 'Playfair Display', serif; /* Headings font */
}

/* =======================
   Buttons
======================= */
/* Remove @apply. Use Tailwind classes in JSX */
button {
  font-family: 'Poppins', sans-serif;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-weight: 600;
  color: #ffffff;
  background-color: #f59e0b; /* highlight fallback */
  transition: background-color 0.3s;
}

button:hover {
  background-color: #d97706; /* hover fallback */
}

/* =======================
   Custom Tailwind Layer (for colors/animations)
======================= */
@layer utilities {
  /* Highlight background */
  .bg-highlight {
    background-color: #f59e0b;
  }
  .hover\:bg-highlight:hover {
    background-color: #d97706;
  }

  /* Animations */
  .animate-fadeIn {
    animation: fadeIn 1.2s ease-in-out forwards;
  }

  .animate-slideUp {
    animation: slideUp 1s ease-out forwards;
  }
}

/* Keyframes */
@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes slideUp {
  0% { transform: translateY(20px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}
